import { useEffect, useState } from 'react';
import { CarService } from '@/services/car-service';
import { CarStatistics } from '@/types';
import { StatisticsService } from '@/services/statistics-service';
import StatusBox from './status-box';
import styles from './status-box.module.css';
import StatusText from './status-text';

interface StatusContainerProps {
  carStatusFilter: '' | '' | '';
  setCarStatusFilter: (status: '' | '' | '') => void;
}

const StatusContainer = ({
  carStatusFilter,
  setCarStatusFilter,
}: StatusContainerProps) => {
  const [carSummary, setCarSummary] = useState<CarStatistics>({
    total: 0,
    operating: 0,
    waiting: 0,
    inspecting: 0,
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchCarStatistics = async () => {
      try {
        setError(null);
        const statistics = await StatisticsService.getCarStatistics();
        setCarSummary(statistics);
      } catch (error) {
        console.error('   :', error);
        setError('   .');
        //     
        setCarSummary({
          total: 0,
          operating: 0,
          waiting: 0,
          inspecting: 0,
        });
      } finally {
        setLoading(false);
      }
    };

    fetchCarStatistics();
  }, []);

  if (loading) {
    return (
      <div className="flex justify-between w-full gap-6 mt-6 px-4 flex-wrap">
        <div className={styles.totalCar}>
          <StatusText num={0} text=" ..." />
        </div>
      </div>
    );
  }

  return (
    <div className="flex justify-between w-full gap-6 mt-6 px-4 flex-wrap">
      {error && (
        <div className="w-full mb-2 p-2 bg-red-100 border border-red-400 text-red-700 rounded">
          {error}
        </div>
      )}
      <div className={styles.totalCar}>
        <StatusText num={carSummary.total} text=" " />
      </div>
      <div
        onClick={() => setCarStatusFilter('')}
        className="cursor-pointer transition-transform duration-200 hover:scale-105"
      >
        <StatusBox
          num={carSummary.operating}
          text=" "
          active={carStatusFilter === ''}
        />
      </div>
      <div
        onClick={() => setCarStatusFilter('')}
        className="cursor-pointer transition-transform duration-200 hover:scale-105"
      >
        <StatusBox
          num={carSummary.waiting}
          text=" "
          active={carStatusFilter === ''}
        />
      </div>
      <div
        onClick={() => setCarStatusFilter('')}
        className="cursor-pointer transition-transform duration-200 hover:scale-105"
      >
        <StatusBox
          num={carSummary.inspecting}
          text=" "
          active={carStatusFilter === ''}
        />
      </div>
    </div>
  );
};

export default StatusContainer;
