import { useEffect, useState } from 'react';
import { CarService } from '@/services/car-service';
import { Car } from '@/types';
import BrandFilterBox from './filter-box';
import floatingStyles from './floating.module.css';
import ListBox from './list-box/list-box';
import NumberSearchBox from './number-search-box';

const SearchBox = () => {
  const [cars, setCars] = useState<Car[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchCars = async () => {
      try {
        const carData = await CarService.getAllCars(1, 50); //   , 50
        setCars(carData.content);
      } catch (error) {
        console.error('   :', error);
        //      
        const fallbackCars: Car[] = [
          { carNumber: '12 1234', brand: '', model: '', status: '' },
          { carNumber: '23 2345', brand: '', model: 'K5', status: '' },
          { carNumber: '34 3456', brand: '', model: 'SM5', status: '' },
        ];
        setCars(fallbackCars);
      } finally {
        setLoading(false);
      }
    };

    fetchCars();
  }, []);

  if (loading) {
    return (
      <div style={{ display: 'flex', justifyContent: 'center', padding: '20px' }}>
        <div> ...</div>
      </div>
    );
  }

  return (
    <>
      <div
        style={{
          display: 'grid',
          gridTemplateColumns: '1fr',
          gap: '4px',
          justifyContent: 'center',
          alignItems: 'center',
          width: '95%',
          position: 'relative',
        }}
      >
        <NumberSearchBox />
        <BrandFilterBox />
        {cars.map((car, idx) => (
          <ListBox
            key={idx}
            carNumber={car.carNumber}
            brand={car.brand}
            model={car.model}
            status={car.status}
          />
        ))}
      </div>
      <div className={floatingStyles.floatingContainer}>
        <button className={floatingStyles.floatingButton}>+</button>
      </div>
    </>
  );
};

export default SearchBox;
