import { create } from 'zustand';

export type Detail = {
  carNumber: string;
  brand: string;
  model: string;
  status: '' | '' | '';
};

type DetailStore = Detail & {
  setDetail: (detail: Detail) => void;
};

export const useDetailStore = create<DetailStore>(set => ({
  carNumber: '',
  brand: '',
  model: '',
  status: '' as '' | '' | '',
  setDetail: detail => set(detail),
}));
