

import { useEffect, useState } from 'react';
import { EmulatorService } from '@/services/emulator-service';
import { Emulator } from '@/types';
import IconButton from '@/components/icon-button/icon-button';
import NumberSearchBox from '@/components/search-box/number-search-box';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import TopBar from '@/components/ui/topBar';
import React from 'react';
import styles from './emulator.module.css';

const handleDelete = async (deviceId: string) => {
  if (!confirm('   ?')) {
    return;
  }

  try {
    await EmulatorService.deleteEmulator(deviceId);
    alert(' .');
    //     
    window.location.reload();
  } catch (error) {
    console.error('  :', error);
    alert('    .');
  }
};

const EmulSearchBox = () => {
  return (
    <div className={styles.emulSearch}>
      <NumberSearchBox />
      <div className={styles.filterContainer}>
        <Input
          type="text"
          placeholder="      . (: 11 1111)"
          className={styles.searchContainer}
        />
        <Button className={styles.searchButton}></Button>
      </div>
    </div>
  );
};

export default function LocalEmulator() {
  const [emulators, setEmulators] = useState<Emulator[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchEmulators = async () => {
      try {
        const emulatorData = await EmulatorService.getAllEmulators(0, 100);
        setEmulators(emulatorData.content);
      } catch (error) {
        console.error('   :', error);
        //      
        const fallbackEmulators: Emulator[] = [
          {
            deviceId: '68fd0215-6a96-11f0-aaf3-0a8c035f5c3b',
            carNumber: '321234',
            emulatorStatus: 'OFF',
          },
          {
            deviceId: '68fd01f8-6a96-11f0-aaf3-0a8c035f5c3b',
            carNumber: '731231',
            emulatorStatus: 'ON',
          },
          {
            deviceId: '68fd01d7-6a96-11f0-aaf3-0a8c035f5c3b',
            carNumber: '125129',
            emulatorStatus: 'OFF',
          },
        ];
        setEmulators(fallbackEmulators);
      } finally {
        setLoading(false);
      }
    };

    fetchEmulators();
  }, []);

const CarEmulNumberSearchBox = () => {
  return (
    <div className={styles.emulSearch}>
      <NumberSearchBox />
      <div className={styles.filterContainer}>
        <Input
          type="text"
          placeholder="      . (: 11 1111)"
          className={styles.searchContainer}
        />
        <Button className={styles.searchButton}></Button>
      </div>
    </div>
  );
};

  return (
    <div>
      <TopBar title=""></TopBar>
      <EmulSearchBox />
      <Table className={styles.emulatorTable}>
        <TableHeader className={styles.tableHeader}>
          <TableRow>
            <TableHead className={styles.tableCellSmall}></TableHead>
            <TableHead className={styles.tableCell}></TableHead>
            <TableHead className={styles.tableCell}> ID</TableHead>
            <TableHead className={styles.tableCell}>ON/OFF</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {emulators.map(emul => (
            <TableRow key={emul.deviceId}>
              <TableCell className={styles.tableCell}>
                <div className={styles.deleteContainer}>
                  <IconButton 
                    iconType="delete" 
                    onClick={() => handleDelete(emul.deviceId)} 
                  />
                </div>
              </TableCell>
              <TableCell className={styles.tableCell}>
                {emul.carNumber}
              </TableCell>
              <TableCell className={styles.tableCell}>
                {emul.deviceId}
              </TableCell>
              <TableCell className={styles.tableCell}>
                {emul.emulatorStatus.toUpperCase()}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
}
